import { PropTypes } from 'prop-types';
import './Input.css';

const Input = ({ handler, title }) => (
  <div className="Input">
    <input
      onChange={(e) => { handler(e.target.value); }}
      type="text"
      value={title}
      placeholder="Search by title"
      data-testid="input"
    />
    {title
      && (
        <button
          type="button"
          title="Clear input"
          onClick={() => {
            handler('');
          }}
        >
          ðŸ—™
        </button>
      )}
  </div>
);

Input.propTypes = {
  title: PropTypes.string.isRequired,
  handler: PropTypes.func.isRequired,
};

export default Input;
