import {
  React,
  useEffect,
} from 'react';
import {
  Switch,
  Route,
  Link,
  Redirect,
  useLocation,
  matchPath,
  useHistory,
} from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import './App.css';
import NewsList from './components/NewsList';
import NewsArticle from './components/NewsArticle';
import DropDown from './components/DropDown';
import Input from './components/Input';
import Spinner from './components/Spinner';
import { storeSelector, articlesSelector } from './store/selectors';
import { setSourceFilter, setTitleFilter, getArticles } from './store/thunks';

const ArticleById = (arr, id) => {
  const result = arr.find((article) => article.id === id);
  return result;
};

function App({
  // eslint-disable-next-line no-shadow
  store, setTitle, setSource, getArticles,
}) {
  const history = useHistory();
  const location = useLocation();
  const { pathname } = location;
  const match = matchPath(pathname, { path: '/:id' });
  const { articles, filters } = store;
  const { title, source } = filters;

  useEffect(() => {
    const params = new URLSearchParams();
    if (source) {
      params.append('source', source);
    } else {
      params.delete('source');
    }

    if (title) {
      params.append('title', title);
    } else {
      params.delete('title');
    }

    history.push({ search: params.toString() });
  }, [source, title, history]);

  useEffect(getArticles, [getArticles]);

  const uniqueSources = new Set(articles?.map((item) => item.source.name));

  const articlesBySource = !source ? articles
    : articles.filter((a) => a.source.name === source);

  const articlesByTitleAndSource = !title ? articlesBySource
    : articlesBySource.filter((a) => a.title.includes(title));

  const selectedArticle = ArticleById(articles, +match?.params.id);

  if (store.error) {
    return (
      <div>
        `Unable to load news = `
        {store.error}
      </div>
    );
  }

  return (
    <div className="app">
      <header className="header-nav">
        <nav>
          <ul>
            <li>
              <Link to="/">Home</Link>
            </li>
          </ul>
        </nav>
        <div className="controls">
          <DropDown list={[...uniqueSources]} label="sources" selected={source} handler={setSource} />
          <Input handler={setTitle} title={title} />
        </div>
      </header>
      <Switch>
        <Route exact path="/">
          {!store.isLoaded && <Spinner />}
          {store.isLoaded && <NewsList list={articlesByTitleAndSource} />}
        </Route>
        <Route exact path="/404">
          <h1>404: Page not found</h1>
        </Route>
        <Route path="/:id">
          {!store.isLoaded && <Spinner />}
          {store.isLoaded && selectedArticle
            && (
              <NewsArticle
                item={selectedArticle}
                handler={() => { history.push('/'); }}
              />
            )}
          {store.isLoaded && !selectedArticle
            && <Redirect to="/404" />}
        </Route>
        <Route>
          <Redirect to="/404" />
        </Route>
      </Switch>
    </div>
  );
}

const storeShape = {
  articles: PropTypes.arrayOf(PropTypes.object).isRequired,
  filters: PropTypes.shape({
    title: PropTypes.string,
    source: PropTypes.string,
  }).isRequired,
  isLoaded: PropTypes.bool.isRequired,
};

App.propTypes = {
  store: PropTypes.shape(storeShape).isRequired,
  setTitle: PropTypes.func.isRequired,
  setSource: PropTypes.func.isRequired,
  getArticles: PropTypes.func.isRequired,
  articles: PropTypes.arrayOf(PropTypes.object).isRequired,
};

const mapStateToProps = (state) => ({
  store: storeSelector(state),
  articles: articlesSelector(state),
});

const mapDispatchToProps = {
  setTitle: setTitleFilter,
  setSource: setSourceFilter,
  getArticles,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
