import {
  SET_FILTER_SOURCE,
  SET_FILTER_TITLE,
  GET_ARTICLES,
} from './actions';

const setSourceFilterCreator = (source) => ({
  type: SET_FILTER_SOURCE,
  payload: source,
});

const setTitleFilterCreator = (title) => ({
  type: SET_FILTER_TITLE,
  payload: title,
});

const getArticlesCreator = (articles) => ({
  type: GET_ARTICLES,
  payload: articles,
});

export {
  setSourceFilterCreator,
  setTitleFilterCreator,
  getArticlesCreator,
};
