import axios from 'axios';
import KEY from '../KEY';
import prepareArticles from '../utils/prepareArticles';
import {
  setSourceFilterCreator,
  setTitleFilterCreator,
  getArticlesCreator,
} from './actionCreators';

const url = (page = 1) => `https://newsapi.org/v2/top-headlines?page=${page}&language=en&pageSize=20&apiKey=${KEY}`;

const getArticles = (page = 1) => (dispatch) => {
  axios.get(url(page)).then(
    ({ data }) => {
      const readyArticles = prepareArticles(data.articles);
      dispatch(getArticlesCreator(readyArticles));
    },
    (err) => dispatch({ type: 'ERROR', payload: err }),
  );
};

const setTitleFilter = (title) => (dispatch) => {
  dispatch(setTitleFilterCreator(title));
};

const setSourceFilter = (source) => (dispatch) => {
  dispatch(setSourceFilterCreator(source));
};

export {
  getArticles,
  setTitleFilter,
  setSourceFilter,
};
