import { React } from 'react';
import { PropTypes } from 'prop-types';
import dateOptions from '../../utils/dateOptions';
import parseTitle from '../../utils/parseTitle';
import './NewsArticle.css';

const NewsArticle = ({ item, handler }) => (
  <article className="NewsArticle">
    <h1>{parseTitle(item.title)}</h1>
    <img src={item.urlToImage || 'https://upload.wikimedia.org/wikipedia/commons/6/65/No-Image-Placeholder.svg'} alt="" width="25%" data-testid="img" />
    <div>
      {(new Date(item.publishedAt)).toLocaleString('en-US', dateOptions)}
    </div>
    <div>{item.source.name}</div>
    <p>{item.content}</p>
    <button onClick={handler} type="button">Back</button>
  </article>
);

const ArticleShape = {
  title: PropTypes.string.isRequired,
  urlToImage: PropTypes.string,
  publishedAt: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  source: PropTypes.shape({
    name: PropTypes.string.isRequired,
  }),
};

NewsArticle.propTypes = {
  handler: PropTypes.func.isRequired,
  item: PropTypes.shape(ArticleShape).isRequired,
};

export default NewsArticle;
