import {
  SET_FILTER_SOURCE,
  SET_FILTER_TITLE,
  GET_ARTICLES,
} from './actions';

const initialState = {
  articles: [],
  filters: {
    title: '',
    source: '',
  },
  isLoaded: false,
  error: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SET_FILTER_SOURCE:
      return {
        ...state,
        filters: {
          ...state.filters,
          source: action.payload,
        },
      };
    case SET_FILTER_TITLE:
      return {
        ...state,
        filters: {
          ...state.filters,
          title: action.payload,
        },
      };
    case GET_ARTICLES: {
      return {
        ...state,
        articles: [...action.payload],
        isLoaded: true,
      };
    }
    default:
      return state;
  }
};
