import { React } from 'react';
import { PropTypes } from 'prop-types';
import dateOptions from '../../utils/dateOptions';
import parseTitle from '../../utils/parseTitle';
import './NewsListItem.css';

const NewsListItem = ({ item, clickHandler, url }) => (
  <>
    <li className="NewsListItem">
      <article>
        <div className="NewsListItem__image-large">
          <img
            src={item.urlToImage || 'https://upload.wikimedia.org/wikipedia/commons/6/65/No-Image-Placeholder.svg'}
            alt=""
            data-testid="img1"
          />
        </div>
        <div className="NewsListItem__details">
          <div className="NewsListItem__image-small">
            <img
              src={item.urlToImage || 'https://upload.wikimedia.org/wikipedia/commons/6/65/No-Image-Placeholder.svg'}
              alt=""
              data-testid="img2"
            />
            <h1>{parseTitle(item.title)}</h1>
          </div>
          <div className="NewsListItem__misc">
            <div>{item.source.name}</div>
            <time dateTime={item.publishedAt}>
              {(new Date(item.publishedAt)).toLocaleString('en-US', dateOptions)}
            </time>
          </div>
          <p>{item.description}</p>
          <a
            href={url}
            onClick={(e) => {
              e.preventDefault();
              clickHandler();
            }}
          >
            Read more
          </a>
        </div>
      </article>
    </li>
  </>
);

const NewsShape = {
  title: PropTypes.string.isRequired,
  urlToImage: PropTypes.string,
  publishedAt: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  source: PropTypes.shape({
    name: PropTypes.string.isRequired,
  }),
};

NewsListItem.propTypes = {
  clickHandler: PropTypes.func.isRequired,
  url: PropTypes.string.isRequired,
  item: PropTypes.shape(NewsShape).isRequired,
};

export default NewsListItem;
